import React from 'react';
import { Container, Table } from 'react-bootstrap';
import StudentList from './../../JSON/StudentList';
import './Student.css';

const Student = () => 
{
    const studentTable = StudentList.map(student => 
    {
        var color;

        if (student.number % 2 === 0)
        {
            color = 'striped';
        }

        return (
            <tr key={student.number} className={color}>
                <td>{student.number}</td>
                <td>{student.id}</td>
                <td>{student.name}</td>
                <td>{student.nick}</td>
            </tr>
        );
    });

    return (
        <>
            <Container className='text-center'>
                <h1>ใบรายชื่อนักเรียนชั้นมัธยมศึกษาปีที่ 4/15</h1><br/>
                <Table responsive bordered>
                    <thead>
                        <tr className='table-secondary'>
                            <th>เลขที่</th>
                            <th>รหัสนักเรียน</th>
                            <th>ชื่อ - นามสกุล</th>
                            <th>ชื่อเล่น</th>
                        </tr>
                        {studentTable}
                        <tr className='table-success'>
                            <th colSpan={4}>ชาย : 21 คน, หญิง : 15 คน<br/>กลุ่ม A : 13 คน, กลุ่ม B : 18 คน, กลุ่ม C : 5 คน</th>
                        </tr>
                    </thead>
                </Table>
            </Container>
        </>
    );
}

export default Student;